body {
	overflow-x: scroll;
	overflow-y: hidden;
	/*width:3840px;*/
	width:5760px;
}
// ------ FLOATS

%floatLeft, .floatLeft {
	float: left;
	display: block;
}
%floatRight, .floatRight {
	float: right;
	display: block;
}

// ------ SPACING

.tSpace, %tSpace {
	margin-top: $defaultMargin*2 !important;
}
.bSpace, %bSpace {
	margin-bottom: $defaultMargin*2 !important;
}
.tSpace--big, %tSpace--big {
	margin-top: $defaultMargin*3 !important;
}
.bSpace--big, %bSpace--big {
	margin-bottom: $defaultMargin*3 !important;
}
.rSpace, %rSpace {
	margin-right: $defaultMargin !important;
}
.tSpace--xbig, %tSpace--xbig {
	margin-top: $defaultMargin*4 !important;
}
.smallSpace {
	margin-top:10px !important;
}
.noSpace {
	margin-top:0px !important;
}

// ------ PANELS

.screen {
	height:1920px;
	width:1080px;
	position: absolute;
	overflow:hidden;

	.cross-fade {
		position: absolute;
		left:0;
		-webkit-transition: opacity 1s ease-in-out;
		-moz-transition: opacity 1s ease-in-out;
		-o-transition: opacity 1s ease-in-out;
		transition: opacity 1s ease-in-out;
		opacity:0;
		-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
		filter: alpha(opacity=0);
	}
	.active {
		opacity:1;
		-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
		filter: alpha(opacity=1);
	}
	.show {
		opacity: 1;
		filter: alpha(opacity=1);
		-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
	}

	.foodContainer {
		overflow: hidden;
	}

}

.absoluteItem {
  position: absolute;
}

.hide {
	display:none;
}

