//http://zerosixthree.se/8-sass-mixins-you-must-have-in-your-toolbox/
// ------- MIXINS, FUNCTIONS, HELPERS

@mixin transition($transition) {
  -webkit-transition: $transition;
  -moz-transition: $transition;
  -ms-transition: $transition;
  -o-transition: $transition;
  transition: $transition;
}

%serif, .serif {
	font-family: serif;
	font-weight: 400;
}

%sansSerif, .sansSerif {
	font-family: sans-serif;
	font-weight: 400;
}

%cursive, .cursive {
	font-family: cursive;
	font-weight: 400;
}

%antialiased {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

%centerBackground {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover !important;
}

%noDecoration {
	text-decoration:none;
	border:none;
}

// ------- COLORS
$c-red: #e91c2b;
$c-yellow: #fff200;

$c-background: black;
$c-text: white;
$c-reverseText: black;

// ---------- BRAND COLORS

$c-facebookBlue: #537bbd;
$c-twitterBlue: #78cdf0;
$c-rssOrange: #f39200;
$c-emailRed: #028628;
$c-pinterestRed: #bd081c;

$colors: (

		red: $c-red,
		yellow: $c-yellow,
		white: white,
		black: black

);

@each $name, $color in $colors {
	.c-#{$name} {
		color: $color;
	}
	.bg-#{$name} {
		background-color: $color;
	}
}

$backgroundOpacity: 0.5;

// ------- SPACING

$navMargin: rems(14);
$defaultMargin: rems(14);
$defaultPadding: rems(20);


// ------- TIMING

$transitionTime: .1s;
$fastTransitionTime: .1s;
$transitionTimeLangSwitcher: .25s;
$transitionTimeScrollIndicator: .75s;


// ------- TYPOGRAPHY

$defaultFontSize: 18;
$smallFontSize: 14;

// Heading font sizes
$fs-hero: 48;
$fs-pageHeading: 48;
$fs-contentHeading: 40;
$fs-sectionHeading: 32;
$fs-cardHeading: 32;
$fs-sidebarHeading: 28;
$fs-textHeading: $defaultFontSize;
$fs-meta: $smallFontSize;

// Component font sizes
$fs-icon: 24;
$fs-toolbarIcon: 24;

// Font-weights
$fw-bold: 800;
$fw-semibold: 700;
$fw-normal: 400;
$fw-light: 300;

// ------- DIMENSIONS

$borderRadius: 1px;
$ovalBorderRadius:8px;
$wideOvalBorderRadius:40px;

$xnarrowWidth: 700px;
$narrowWidth: 960px;
$standardWidth: 1280px;
$fenceWidth: 1280px;
$bodyWidth: 1920px;


$breakpoints: (
	standard: (min: 0px, max: 999999px),
	fence: (min: 0px, max: $fenceWidth),
	smallphone: (min: 0px, max: 480px),
	small: (min: 0px, max: 767px),
	medium: (min: 766px, max: 946px),
	design: (min: 947px, max: 1023px),
	large: (min: 1024px, max: 1280px),
	verylarge: (min: 1281px, max: 1654px),
	superwide: (min: 1500px, max: 999999px),
);
