// ReFrame Media standard Queries
@mixin mqIs($name: standard, $device: screen) {

	$bp: map-get($breakpoints, $name);
	$min: map-get($bp, min);
	$max: map-get($bp, max);

	@media only #{$device} and (min-width: $min) and (max-width: $max)
	{
		@content;
	}

}

@mixin mqAtLeast($name: standard, $device: screen) {

	$bp: map-get($breakpoints, $name);
	$min: map-get($bp, min);

	@media only #{$device} and (min-width: $min)
	{
		@content;
	}

}

@mixin mqLargerThan($name: standard, $device: screen) {

	$bp: map-get($breakpoints, $name);
	$max: map-get($bp, max);

	@media only #{$device} and (min-width: $max)
	{
		@content;
	}

}

@mixin mqNoLargerThan($name: standard, $device: screen) {

	$bp: map-get($breakpoints, $name);
	$max: map-get($bp, max);

	@media only #{$device} and (max-width: $max)
	{
		@content;
	}

}

@mixin mqSmallerThan($name: standard, $device: screen) {

	$bp: map-get($breakpoints, $name);
	$min: map-get($bp, min);

	@media only #{$device} and (max-width: $min)
	{
		@content;
	}

}

// ------- GLOBAL HIDE / SHOW

.hideSmall {
	diplay:block !important;

	@include mqNoLargerThan(small) {
		display: none !important;
	}
}

.showSmallOnly {
	display: none !important;

	@include mqNoLargerThan(small) {
		display: block !important;
	}
}

.hideTablet {
	diplay:block !important;

	@include mqNoLargerThan(design) {
		display: none !important;
	}
}

.showTabletOnly {
	display: none !important;

	@include mqNoLargerThan(design) {
		display: block !important;
	}
}

@mixin fencedWidth() {

	width: 90%;
	max-width: $fenceWidth;
	margin-left: auto;
	margin-right: auto;

	@include mqSmallerThan(large) {
		width: 100%;
	}

}